.example {
  color: #424242;
  max-width: 960px;
  background-color: #fff;
  padding: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.example code {
  color: #fff;
  background-color: #009688;
  border-radius: 2px;
  padding: 2px 6px;
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
}

.example .background {
  height: 240px;
  background-image: url("demo.e3ba131c.jpg");
  background-position: center;
  background-size: cover;
}

.backdrop-modal {
  z-index: 23;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: #1116;
  transition: opacity .7s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.backdrop-modal.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.modal {
  z-index: 22;
  opacity: 1;
  transition: opacity .7s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.modal.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.pop-up-modal {
  width: 335px;
  height: 806px;
  background-color: #f6f6f6;
  border: 2px solid #040404;
  border-radius: 18px;
  padding: 40px 24px;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 400;
  display: inline-block;
  position: relative;
  top: 430px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.close-modal-btn {
  width: 28px;
  height: 28px;
  cursor: pointer;
  background: #f6f6f6;
  border: none;
  padding: 0;
  transition: fill .3s;
  position: absolute;
  top: 7px;
  right: 7px;
}

.close-modal-btn:hover {
  fill: #4f2ee8;
}

.image {
  width: 287px;
  height: 408px;
  border-radius: 8px;
}

.title {
  margin-top: 14px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.author {
  color: #b4afaf;
  margin-top: 4px;
  font-size: 12px;
  font-style: italic;
  line-height: 14px;
}

.description {
  width: 287px;
  margin-top: 18px;
  font-size: 14px;
  line-height: 18px;
}

.buy-links {
  width: 115px;
  filter: grayscale();
  justify-content: start;
  align-items: center;
  gap: 20px;
  margin-top: 14px;
  display: flex;
}

.buy-links-item {
  transition: filter .3s;
}

.buy-links-item:hover {
  filter: drop-shadow(0 3px 3px #00000026);
}

.buttons {
  text-transform: uppercase;
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  padding: 14px 23px;
  font-size: 13px;
  font-weight: 700;
  line-height: 18px;
  transition: background, color .3s;
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
}

.buttons:hover {
  color: #fff;
  cursor: pointer;
  background: #4f2ee8;
}

.add-book-button {
  width: 221px;
}

.delete-book-button {
  width: 279px;
}

.remove-button {
  display: none;
}

